.mySwiper {
    width: 100%;
}

.mySwiper .swiper-wrapper {
    width: 100%;
}


.mySwiper .swiper-slide {
    width: 100% !important;
    height: 700px;

}

.mySwiper .swiper-slide img {
    width: 100%;
    position: relative;
}

.mySwiper .slide_txt {
    position: absolute;
    top: 30%;
    left: 20%;
}

.mySwiper .slide_txt h1 {
    display: block;
    color: #fff;
    font-size: 66px;
    line-height: 80px;
    letter-spacing: -3px;
}


.mySwiper .slide_txt h3 {
    display: block;
    height: 40px;
    color: #fff;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: 0;
    margin: 0 0 28px 5px;
    
}

.mySwiper .swiper-button-prev {color:#fff;}

.mySwiper .swiper-button-next {color:#fff;}

.mySwiper .swiper-pagination {bottom:40px;}

.mySwiper .slide_txt p { display: block;
    color: #fff;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0px;
    margin-left: 5px;
    margin-top:40px;
}