.info_group {margin-top: 21px; width:530px; height:353px; }

.info_group li {width: 560px;height:30px; display:block; padding: 10px 0;
    line-height: 28px;
    border-bottom: 1px solid #eee;}

.info_group li:first-of-type {width: 560px;height:51px; padding:10px 0 10px 42px; }

.info_group li:first-of-type > .icon {width:30px; height:30px; float:left; margin:0 0 0 -40px;}

.info_group li:first-of-type > .icon > img {width:100%; height:100%;}

.info_group li:first-of-type > .bold {font-weight:bold; float:left; margin:0  0 10px;}

.info_group li:first-of-type > .li {float:left; font-size: 15px; margin:0 0 0 10px;}

.info_group li:nth-of-type(2) {width:560px; height:118px; }

.info_group li:nth-of-type(2) > .icon {width:30px; height:30px; float:left; }

.info_group li:nth-of-type(2) > .icon > img {width:100%; height:100%;}

.info_group li:nth-of-type(2) .bold {font-weight:bold; float:left; margin:0  0  0 10px;}

.info_group li:nth-of-type(2) > .li { float:left; text-align:left; width:518px; height:17px; font-size: 15px; margin:0 0 0 10px;}

.info_group .arrive{width:518px; height:35px;  float:left; text-align:left; margin:10px 0 0 35px;}

.info_group li:nth-of-type(3) {width:560px; height:51px;  }

.info_group li:nth-of-type(3) .icon {width:30px; height:30px; float:left;}

.info_group li:nth-of-type(3) > .icon > img {width:100%; height:100%;}

.info_group li:nth-of-type(3) > .bold {font-weight:bold; float:left; margin:0  0 0 10px;}

.info_group li:nth-of-type(3) .li {font-size:13px; float:left; margin:0 0 0 10px;}

.info_group li:nth-of-type(4) {width:561px; height:51px; }

.info_group li:nth-of-type(4) .icon {width:30px; height:30px; float:left;}


.info_group li:nth-of-type(4) > .icon > img {width:100%; height:100%;}

.info_group li:nth-of-type(4) > .bold {width:44px; height:12px; float:left; margin:0 0 0 10px;}

.info_group li:nth-of-type(4) > .bold  > img {width:100%; height:100%;}

.info_group li:nth-of-type(4) .li { float:left; font-size:13px; margin:0  0  0 10px;}

.info_group li:nth-of-type(5) {width:560px; height:89px;  text-align:left;}

.info_group li:nth-of-type(5) input {display:block; width:558px; height:44px;}

